@import "../../global.scss";

.menu {
    width: 300px;
    height: 100vh;
    background-color: white;
    position: fixed;
    top: 0;
    right: -300px;
    z-index: 9999;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: all 1s ease;

    &.dark {
        color: white;
        background-color: #303437
    }

    &.active {
        right: 0;
    }

    ul {
        margin: 0;
        padding: 0;
        list-style-type: none;
        font-size: 40px;
        color: inherit;
        width: 60%;

     

        li {
            margin-bottom: 20px;
            a {
                font-size: inherit;
                color: inherit;
                text-decoration: none;
            }

            &:hover {
                font-weight: 700;
            }
        }
    }
}